@import url(https://fonts.googleapis.com/css2?family=Rampart+One&family=Poiret+One&family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background-color:tomato;display:flex;flex-direction:column;align-items:center;justify-content:center}.App{margin-top:3vh;text-align:center}.App svg{background-color:tomato;border-radius:4px;box-shadow:rgba(0,0,0,0.2) 0px 60px 40px -7px}.App svg #title{font-family:"Press Start 2P", cursive;font-size:1.5em;fill:#06720f;text-shadow:2px 2px 0 #fff}.App svg #gdp{fill:white;font-family:"Poiret One", cursive;letter-spacing:2px;text-shadow:1px 1px 0 #06720f}.App svg .bar{fill:#ffbf47}.App svg .bar:hover{fill:white}.App svg line{stroke:#06720f}.App svg path{stroke:#06720f}.App svg text{fill:#06720f;font-weight:650}.App #tooltip{display:flex;align-items:center;justify-content:center;position:absolute;text-align:center;width:100px;height:50px;padding:10px;background-color:white;border-radius:5px}.App #da3ker{color:white;font-size:0.9em;letter-spacing:2px;font-family:"Rampart One", cursive;margin:auto}

